* {
    margin: 0;
    padding: 0px;
    box-sizing: border-box;
    
}

ul, li {
    list-style: none;
}

a {
    text-decoration: none;
}


::-webkit-scrollbar-track
{
	border: 1px solid black;
	background-color: #F5F5F5;
}

::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb
{
	background-color: #464646;	
}